@font-face {
  font-family: 'Oswald';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  font-weight: 400;
  font-style: normal;
}

@font-face {
  font-family: 'Anton';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
  font-weight: 800;
  font-style: bold;
}

@font-face {
  font-family: 'Wall Poet';
  src: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
  font-weight: 600;
  /* font-style: Bold; */
}

/* TODO: MOVE CUSTOM CSS PROPERTIES TO GLOBALS.CSS LATER */
body {
  /* Menu */
  --body-bg: linear-gradient(to right, hsl(214, 94%, 12%), hsl(214, 94%, 19%));
  --menu-text-color: white;
  --menu-bg-color: hsla(225, 85%, 5%, 1);

  /* Game grids */
  --gap: 0.125rem;
  --gameboard-margin: 1rem;
  --grid-size: 10;
  --color-cell-own: hsl(100, 75%, 87%);
  --color-cell-guess: hsl(0, 75%, 87%);

  /* Fonts */
  --rwd-text-xs: calc(0.25rem + 2vw);
  --rwd-text-sm: calc(2.5vw);
  --rwd-text-md: calc(0.75rem + 3vw);

  /* Colors */
  --black: black;
  --white: white;
  --text-color-1: LightGray;
  --primary-color: hsl(225, 89%, 55%);
  --primary-color-grad1: hsl(274, 89%, 50%);
  --secondary-color: hsl(195, 89%, 55%);

  /* Spacing */
  --space-sm: calc(0.5vw);
  --space-md: calc(1rem + 1vw);

  /* Border */
  --border-radius: 7px;

  /* body properties */
  background: var(--body-bg);
  font: 1.25rem 'Oswald';
  color: var(--text-color-1);
  letter-spacing: 0.25rem;
  min-height: 100vh;
}

.main {
  display: grid;
  align-items: center;
  min-height: 100vh;
}

.main-display {
  margin-block: var(--space-md);
}

.menu__header {
  text-align: center;
}

.menu__pretitle {
  font: Bold var(--rwd-text-md) 'Wall Poet';
  letter-spacing: 0.5rem;
  text-shadow: 1px 1px 7px var(--secondary-color);
  color: var(--black);
}

.menu__title {
  font: 15vw 'Anton';
  background-image: linear-gradient(
    var(--primary-color),
    var(--primary-color-grad1)
  );
  color: transparent;
  background-clip: text;
  transform: perspective(1000px) rotate3D(1, 0, 0, 20deg);
}

.menu__body {
  font-size: 1.25rem;
  /* z-index: 1; */
  display: grid;
  gap: var(--space-md);
}

.menu__body--select_opponent {
}

.menu__body-header {
  text-align: center;
  font-size: var(--rwd-text-sm);
}

.menu__button {
  font-size: var(--rwd-text-sm);
  letter-spacing: 0.125rem;
  padding-block: calc(0.125rem + 1vw);
  border: 2px solid var(--secondary-color);
  color: var(--secondary-color);
  border-radius: var(--border-radius);
  background: transparent;
  cursor: pointer;
  transition: transform 150ms;
}

.menu__button:hover {
  color: var(--black);
  background: var(--secondary-color);
  transform: scaleY(1.15);
}

.menu__button:focus {
  outline: 3px dashed white;
  outline-offset: 3px;
}

.menu__field-group {
  display: grid;
  grid-auto-flow: column;
  align-items: center;
  /* justify-items: center; */
}

.menu__label {
  font-size: var(--rwd-text-sm);
  color: var(--white);
  justify-self: center;
}

.menu__text-input {
  font-size: var(--rwd-text-sm);
  padding-inline: 1vw;
  padding-block: 0.5vw;
  letter-spacing: 0.125rem;
  color: var(--primary-color);
}

.placement {
  color: var(--text-color-1);
  display: grid;
  gap: var(--space-md);
}

.placement__header {
  text-align: center;
}

.placement__player-heading {
  font-size: var(--rwd-text-sm);
}

.placement__instructions {
  font-size: var(--rwd-text-md);
  color: White;
}

.placement__instructions-subtext {
  font-size: var(--rwd-text-xs);
  font-style: italic;
  color: gold;
}

.placement__body {
  display: grid;
  gap: var(--space-md);

  grid-template-areas:
    'p_grid'
    'p_btns'
    'p_bank';
}

.placement__buttons {
  grid-area: p_btns;
  display: grid;
  grid-auto-flow: column;
  gap: var(--space-md);
  height: max-content;
}

.placement__button {
  font-size: var(--rwd-text-xs);
  letter-spacing: 0.125rem;
  padding-block: 0.5vw;
  border: 2px solid var(--secondary-color);
  color: var(--secondary-color);
  border-radius: var(--border-radius);
  background: transparent;
  cursor: pointer;
  transition: transform 150ms;
}

.placement__button:hover {
  text-decoration: underline;
}

button[disabled='disabled'],
button:disabled {
  /* background: var(--primary-color); */
  color: Gray;
  border-color: Gray;
  /* border-color: var(--primary-color); */
  pointer-events: none;
}

.game-grid {
  position: relative;
  grid-area: p_grid;
  background: radial-gradient(
    circle,
    hsl(185, 75%, 55%) 0%,
    hsl(179, 75%, 45%) 35%,
    hsl(169, 75%, 35%) 100%
  );

  display: grid;
  grid-template-rows: repeat(11, 1fr);
  grid-template-columns: repeat(11, 1fr);
}

.cell {
  display: grid;
  place-items: center;
  position: relative;
  width: 100%;
  padding-top: 100%;
  /*FIXME: We need to use a background on cell, so that we can change its background color on attack. Maybe use cell bg + grid bg and mix-blend-mode*/
  /* background: black; */
  outline: 2px solid hsla(185, 100%, 75%, 0.75);
  box-shadow: -3px 3px 3px hsla(0, 75%, 1%, 0.15);
}

.axis-marker {
  position: relative;
  display: grid;
  place-items: center;
  pointer-events: none;
}

.axis-marker-text {
  position: absolute;
  color: hsla(185, 100%, 75%, 0.75);
  font-family: 'Wall Poet';
  font-style: italic;
  font-size: var(--rwd-text-sm);
  text-shadow: hsla(0, 75%, 1%, 0.15)-3px 3px;
  pointer-events: none;
}

.bank {
  --cell-size: 8cqw;
  text-align: center;
  background: hsla(0, 0%, 0%, 0.25);
}

.bank-header {
  margin-top: var(--space-sm);
}

.ships {
  width: 100%;
  min-height: calc(5 * var(--cell-size));
  display: flex;
  align-content: start;
  justify-content: center;
  flex-wrap: wrap;
  gap: var(--cell-size);
}

.ship-wrapper {
  width: calc(var(--ship-length) * var(--cell-size));
  height: calc(1 * var(--cell-size));
  cursor: grab;
}

.ship-wrapper.rotated {
  width: calc(1 * var(--cell-size));
  height: calc(var(--ship-length) * var(--cell-size));
}

.ship-wrapper[data-ship='Carrier'] {
  --ship-length: 5;
}
.ship-wrapper[data-ship='Battleship'] {
  --ship-length: 4;
}
.ship-wrapper[data-ship='Destroyer'] {
  --ship-length: 3;
}
.ship-wrapper[data-ship='Submarine'] {
  --ship-length: 3;
}
.ship-wrapper[data-ship='PatrolBoat'] {
  --ship-length: 2;
}

.ship-image {
  width: 100%;
  height: 100%;
}

.ship-rotated-image {
  width: 100%;
  height: 100%;
  display: none; /*change programatically*/
}

.ship-wrapper.dragging {
  opacity: 0.25;
}

/* When placed */
.ship-wrapper.placed {
  position: absolute;
  grid-row: 4 / span 1;
  grid-column: 4 / span 3;
  width: 100%;
  height: 100%;
}

.take-turn {
}

.take-turn__header {
  text-align: center;
  margin-bottom: var(--space-sm);
}

.take-turn__header > span {
  display: block;
  text-align: center;
  font-size: var(--rwd-text-sm);
  font-style: italic;
  color: gold;
}

.take-turn__header > span:first-child {
  display: block;
  text-align: center;
  font-size: var(--rwd-text-md);
  font-style: normal;
  color: white;
}

.take-turn__grid-group {
  display: grid;
  justify-items: center;
  text-align: center;
}

.take-turn__grid-group > * {
}

.game-grid.current-turn {
  grid-area: initial;
}

.game-grid--own.current-turn {
  width: 35vw;
  height: 35vw;
  background: radial-gradient(
    circle,
    hsl(185, 75%, 45%) 0%,
    hsl(179, 75%, 40%) 35%,
    hsl(169, 75%, 35%) 100%
  );
}

.game-grid--own.current-turn > .cell {
  outline: 2px solid hsla(185, 100%, 40%, 0.75);
}

.game-grid--guesses.current-turn {
  position: relative;
  width: 65vw;
  height: 65vw;
  background: hsl(185, 100%, 8%);
}

.game-grid--guesses.current-turn > .cell {
  cursor: 0.5vw solid hsla(185, 100%, 40%, 0.75);
}

.game-grid--guesses.current-turn > .cell:hover {
  cursor: crosshair;
  background: hsl(185, 100%, 15%);
}

.crosshairX {
  position: absolute;
  height: 0px;
  width: 100%;
  border-top: 2px dashed Red;
  pointer-events: none;
}
.crosshairY {
  position: absolute;
  width: 0px;
  height: 100%;
  border-left: 4px solid Red;
  pointer-events: none;
}

.torpedo {
  height: 75px;
  aspect-ratio: 1;
  position: absolute;
  background:;
  pointer-events: none;
  opacity: 0;
}

.torpedo-image {
  height: 100%;
}

.dot {
  position: absolute;
  width: 30%;
  aspect-ratio: 1;
  border-radius: 50%;
}
.dot.hit {
  background: red;
}
.dot.miss {
  background: white;
}

.game-over-modal {
  position: absolute;
  padding: 3rem;
  background: teal;
  font-size: var(--rwd-text-md);
  color: white;
  top: 25%;
  width: 100%;
  /* left: 15%; */
}

.begin-game-dialog {
  position: absolute;
  display: flex;
  place-content: center;
  text-align: center;
  top: 15%;
  left: 0;
  width: 100%;
  height: 50%;
  background: navy;
  /* margin: 0 auto; */
  z-index: 9999;
}

.begin-game-dialog__text {
  margin: var(--space-md);
  color: gold;
}

.menu__button--dialog {
  padding-inline: var(--space-md);
}

@media screen and (min-width: 500px) {
  body {
    margin-inline: var(--space-md);
  }
}

@media screen and (min-width: 900px) {
  .placement__body {
    grid-template-columns: 65% 1fr;
    grid-template-areas:
      'p_grid p_bank'
      'p_btns p_btns';
  }

  .bank {
    --cell-size: 5.25cqw;
  }

  .ships {
    margin-top: var(--space-md);
  }
}
