C;AACF;;AAEA;EACE;IACE,8BAA8B;IAC9B;;qBAEiB;EACnB;;EAEA;IACE,oBAAoB;EACtB;;EAEA;IACE,2BAA2B;EAC7B;AACF","sourcesContent":["/* style.css */\n\n@font-face {\n  font-family: 'Oswald';\n  src: url('./assets/fonts/Oswald/Oswald-VariableFont_wght.ttf');\n  font-weight: 400;\n  font-style: normal;\n}\n\n@font-face {\n  font-family: 'Anton';\n  src: url('./assets/fonts/Anton/Anton-Regular.ttf');\n  font-weight: 800;\n  font-style: bold;\n}\n\n@font-face {\n  font-family: 'Wall Poet';\n  src: url('./assets/fonts/Wallpoet/Wallpoet-Regular.ttf');\n  font-weight: 600;\n  /* font-style: Bold; */\n}\n\n/* TODO: MOVE CUSTOM CSS PROPERTIES TO GLOBALS.CSS LATER */\nbody {\n  /* Menu */\n  --body-bg: linear-gradient(to right, hsl(214, 94%, 12%), hsl(214, 94%, 19%));\n  --menu-text-color: white;\n  --menu-bg-color: hsla(225, 85%, 5%, 1);\n\n  /* Game grids */\n  --gap: 0.125rem;\n  --gameboard-margin: 1rem;\n  --grid-size: 10;\n  --color-cell-own: hsl(100, 75%, 87%);\n  --color-cell-guess: hsl(0, 75%, 87%);\n\n  /* Fonts */\n  --rwd-text-xs: calc(0.25rem + 2vw);\n  --rwd-text-sm: calc(2.5vw);\n  --rwd-text-md: calc(0.75rem + 3vw);\n\n  /* Colors */\n  --black: black;\n  --white: white;\n  --text-color-1: LightGray;\n  --primary-color: hsl(225, 89%, 55%);\n  --primary-color-grad1: hsl(274, 89%, 50%);\n  --secondary-color: hsl(195, 89%, 55%);\n\n  /* Spacing */\n  --space-sm: calc(0.5vw);\n  --space-md: calc(1rem + 1vw);\n\n  /* Border */\n  --border-radius: 7px;\n\n  /* body properties */\n  background: var(--body-bg);\n  font: 1.25rem 'Oswald';\n  color: var(--text-color-1);\n  letter-spacing: 0.25rem;\n  min-height: 100vh;\n}\n\n.main {\n  display: grid;\n  align-items: center;\n  min-height: 100vh;\n}\n\n.main-display {\n  margin-block: var(--space-md);\n}\n\n.menu__header {\n  text-align: center;\n}\n\n.menu__pretitle {\n  font: Bold var(--rwd-text-md) 'Wall Poet';\n  letter-spacing: 0.5rem;\n  text-shadow: 1px 1px 7px var(--secondary-color);\n  color: var(--black);\n}\n\n.menu__title {\n  font: 15vw 'Anton';\n  background-image: linear-gradient(\n    var(--primary-color),\n    var(--primary-color-grad1)\n  );\n  color: transparent;\n  background-clip: text;\n  transform: perspective(1000px) rotate3D(1, 0, 0, 20deg);\n}\n\n.menu__body {\n  font-size: 1.25rem;\n  /* z-index: 1; */\n  display: grid;\n  gap: var(--space-md);\n}\n\n.menu__body--select_opponent {\n}\n\n.menu__body-header {\n  text-align: center;\n  font-size: var(--rwd-text-sm);\n}\n\n.menu__button {\n  font-size: var(--rwd-text-sm);\n  letter-spacing: 0.125rem;\n  padding-block: calc(0.125rem + 1vw);\n  border: 2px solid var(--secondary-color);\n  color: var(--secondary-color);\n  border-radius: var(--border-radius);\n  background: transparent;\n  cursor: pointer;\n  transition: transform 150ms;\n}\n\n.menu__button:hover {\n  color: var(--black);\n  background: var(--secondary-color);\n  transform: scaleY(1.15);\n}\n\n.menu__button:focus {\n  outline: 3px dashed white;\n  outline-offset: 3px;\n}\n\n.menu__field-group {\n  display: grid;\n  grid-auto-flow: column;\n  align-items: center;\n  /* justify-items: center; */\n}\n\n.menu__label {\n  font-size: var(--rwd-text-sm);\n  color: var(--white);\n  justify-self: center;\n}\n\n.menu__text-input {\n  font-size: var(--rwd-text-sm);\n  padding-inline: 1vw;\n  padding-block: 0.5vw;\n  letter-spacing: 0.125rem;\n  color: var(--primary-color);\n}\n\n.placement {\n  color: var(--text-color-1);\n  display: grid;\n  gap: var(--space-md);\n}\n\n.placement__header {\n  text-align: center;\n}\n\n.placement__player-heading {\n  font-size: var(--rwd-text-sm);\n}\n\n.placement__instructions {\n  font-size: var(--rwd-text-md);\n  color: White;\n}\n\n.placement__instructions-subtext {\n  font-size: var(--rwd-text-xs);\n  font-style: italic;\n  color: gold;\n}\n\n.placement__body {\n  display: grid;\n  gap: var(--space-md);\n\n  grid-template-areas:\n    'p_grid'\n    'p_btns'\n    'p_bank';\n}\n\n.placement__buttons {\n  grid-area: p_btns;\n  display: grid;\n  grid-auto-flow: column;\n  gap: var(--space-md);\n  height: max-content;\n}\n\n.placement__button {\n  font-size: var(--rwd-text-xs);\n  letter-spacing: 0.125rem;\n  padding-block: 0.5vw;\n  border: 2px solid var(--secondary-color);\n  color: var(--secondary-color);\n  border-radius: var(--border-radius);\n  background: transparent;\n  cursor: pointer;\n  transition: transform 150ms;\n}\n\n.placement__button:hover {\n  text-decoration: underline;\n}\n\nbutton[disabled='disabled'],\nbutton:disabled {\n  /* background: var(--primary-color); */\n  color: Gray;\n  border-color: Gray;\n  /* border-color: var(--primary-color); */\n  pointer-events: none;\n}\n\n.game-grid {\n  position: relative;\n  grid-area: p_grid;\n  background: radial-gradient(\n    circle,\n    hsl(185, 75%, 55%) 0%,\n    hsl(179, 75%, 45%) 35%,\n    hsl(169, 75%, 35%) 100%\n  );\n\n  display: grid;\n  grid-template-rows: repeat(11, 1fr);\n  grid-template-columns: repeat(11, 1fr);\n}\n\n.cell {\n  display: grid;\n  place-items: center;\n  position: relative;\n  width: 100%;\n  padding-top: 100%;\n  /*FIXME: We need to use a background on cell, so that we can change its background color on attack. Maybe use cell bg + grid bg and mix-blend-mode*/\n  /* background: black; */\n  outline: 2px solid hsla(185, 100%, 75%, 0.75);\n  box-shadow: -3px 3px 3px hsla(0, 75%, 1%, 0.15);\n}\n\n.axis-marker {\n  position: relative;\n  display: grid;\n  place-items: center;\n  pointer-events: none;\n}\n\n.axis-marker-text {\n  position: absolute;\n  color: hsla(185, 100%, 75%, 0.75);\n  font-family: 'Wall Poet';\n  font-style: italic;\n  font-size: var(--rwd-text-sm);\n  text-shadow: hsla(0, 75%, 1%, 0.15)-3px 3px;\n  pointer-events: none;\n}\n\n.bank {\n  --cell-size: 8cqw;\n  text-align: center;\n  background: hsla(0, 0%, 0%, 0.25);\n}\n\n.bank-header {\n  margin-top: var(--space-sm);\n}\n\n.ships {\n  width: 100%;\n  min-height: calc(5 * var(--cell-size));\n  display: flex;\n  align-content: start;\n  justify-content: center;\n  flex-wrap: wrap;\n  gap: var(--cell-size);\n}\n\n.ship-wrapper {\n  width: calc(var(--ship-length) * var(--cell-size));\n  height: calc(1 * var(--cell-size));\n  cursor: grab;\n}\n\n.ship-wrapper.rotated {\n  width: calc(1 * var(--cell-size));\n  height: calc(var(--ship-length) * var(--cell-size));\n}\n\n.ship-wrapper[data-ship='Carrier'] {\n  --ship-length: 5;\n}\n.ship-wrapper[data-ship='Battleship'] {\n  --ship-length: 4;\n}\n.ship-wrapper[data-ship='Destroyer'] {\n  --ship-length: 3;\n}\n.ship-wrapper[data-ship='Submarine'] {\n  --ship-length: 3;\n}\n.ship-wrapper[data-ship='PatrolBoat'] {\n  --ship-length: 2;\n}\n\n.ship-image {\n  width: 100%;\n  height: 100%;\n}\n\n.ship-rotated-image {\n  width: 100%;\n  height: 100%;\n  display: none; /*change programatically*/\n}\n\n.ship-wrapper.dragging {\n  opacity: 0.25;\n}\n\n/* When placed */\n.ship-wrapper.placed {\n  position: absolute;\n  grid-row: 4 / span 1;\n  grid-column: 4 / span 3;\n  width: 100%;\n  height: 100%;\n}\n\n.take-turn {\n}\n\n.take-turn__header {\n  text-align: center;\n  margin-bottom: var(--space-sm);\n}\n\n.take-turn__header > span {\n  display: block;\n  text-align: center;\n  font-size: var(--rwd-text-sm);\n  font-style: italic;\n  color: gold;\n}\n\n.take-turn__header > span:first-child {\n  display: block;\n  text-align: center;\n  font-size: var(--rwd-text-md);\n  font-style: normal;\n  color: white;\n}\n\n.take-turn__grid-group {\n  display: grid;\n  justify-items: center;\n  text-align: center;\n}\n\n.take-turn__grid-group > * {\n}\n\n.game-grid.current-turn {\n  grid-area: initial;\n}\n\n.game-grid--own.current-turn {\n  width: 35vw;\n  height: 35vw;\n  background: radial-gradient(\n    circle,\n    hsl(185, 75%, 45%) 0%,\n    hsl(179, 75%, 40%) 35%,\n    hsl(169, 75%, 35%) 100%\n  );\n}\n\n.game-grid--own.current-turn > .cell {\n  outline: 2px solid hsla(185, 100%, 40%, 0.75);\n}\n\n.game-grid--guesses.current-turn {\n  position: relative;\n  width: 65vw;\n  height: 65vw;\n  background: hsl(185, 100%, 8%);\n}\n\n.game-grid--guesses.current-turn > .cell {\n  cursor: 0.5vw solid hsla(185, 100%, 40%, 0.75);\n}\n\n.game-grid--guesses.current-turn > .cell:hover {\n  cursor: crosshair;\n  background: hsl(185, 100%, 15%);\n}\n\n.crosshairX {\n  position: absolute;\n  height: 0px;\n  width: 100%;\n  border-top: 2px dashed Red;\n  pointer-events: none;\n}\n.crosshairY {\n  position: absolute;\n  width: 0px;\n  height: 100%;\n  border-left: 4px solid Red;\n  pointer-events: none;\n}\n\n.torpedo {\n  height: 75px;\n  aspect-ratio: 1;\n  position: absolute;\n  background:;\n  pointer-events: none;\n  opacity: 0;\n}\n\n.torpedo-image {\n  height: 100%;\n}\n\n.dot {\n  position: absolute;\n  width: 30%;\n  aspect-ratio: 1;\n  border-radius: 50%;\n}\n.dot.hit {\n  background: red;\n}\n.dot.miss {\n  background: white;\n}\n\n.game-over-modal {\n  position: absolute;\n  padding: 3rem;\n  background: teal;\n  font-size: var(--rwd-text-md);\n  color: white;\n  top: 25%;\n  width: 100%;\n  /* left: 15%; */\n}\n\n.begin-game-dialog {\n  position: absolute;\n  display: flex;\n  place-content: center;\n  text-align: center;\n  top: 15%;\n  left: 0;\n  width: 100%;\n  height: 50%;\n  background: navy;\n  /* margin: 0 auto; */\n  z-index: 9999;\n}\n\n.begin-game-dialog__text {\n  margin: var(--space-md);\n  color: gold;\n}\n\n.menu__button--dialog {\n  padding-inline: var(--space-md);\n}\n\n@media screen and (min-width: 500px) {\n  body {\n    margin-inline: var(--space-md);\n  }\n}\n\n@media screen and (min-width: 900px) {\n  .placement__body {\n    grid-template-columns: 65% 1fr;\n    grid-template-areas:\n      'p_grid p_bank'\n      'p_btns p_btns';\n  }\n\n  .bank {\n    --cell-size: 5.25cqw;\n  }\n\n  .ships {\n    margin-top: var(--space-md);\n  }\n}\n"],"sourceRoot":""}]);
